import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';
import { Observable } from 'rxjs';
import { LoggedInCallback, CognitoUtil, Callback } from '../service/cognito.service';
import { UserLoginService } from '../service/user-login.service';
import { Router } from '@angular/router';


@Injectable()
export class JwtInterceptor implements HttpInterceptor, LoggedInCallback  {
    public idToken: string;
    constructor(public router: Router, public userService: UserLoginService, public cognitoUtil: CognitoUtil) {
        this.userService.isAuthenticated(this);
        console.log("in Jwt Interceptor");

    }

    isLoggedIn(message: string, isLoggedIn: boolean) {
        if (!isLoggedIn) {
            this.router.navigate(['/home/login']);
        } else {
            this.cognitoUtil.getIdToken(new IdTokenCallback(this));
        }
    }

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        // let currentUser = this.cognitoUtil.getCurrentUser().
        // if (currentUser && currentUser.token) {
            request = request.clone({
                setHeaders: { 
                    Authorization: `Bearer ${this.idToken}`
                }
            });
        // }
        // console.log(this.idToken)
        return next.handle(request);
    }
}

export class IdTokenCallback implements Callback {
    constructor(public jwt: JwtInterceptor) {

    }

    callback() {

    }

    callbackWithParam(result) {
        this.jwt.idToken = result;
    }
}